* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

#game-canvas {
  z-index: 1;
  background-image: url("background.f7c632b5.png");
  background-repeat: repeat;
  background-size: cover;
  width: 100vw;
  height: 100vh;
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  transform: none;
}

#overlay {
  object-fit: cover;
  pointer-events: none;
  z-index: 2;
  width: 100vw;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
}

#obstacles, #bull, #egg, #toad, #larva {
  display: none;
}

